.TopicPage {
    padding-top: 16px;
}

.TopicPage .Topic {
    background: #FFFFFF;
    box-shadow: 0 1px 3px 0 #D9DDE5;
    border-radius: 5px;
}

.TopicPage .Topic .TopicWrapper {
    padding: 32px;
}

.TopicPage .Topic .Title {
    font-size: 32px;
    font-weight: 700;
    margin-bottom: 0;
    margin-top: 0;
}

.TopicPage .Topic .Description {
    margin-top: 8px;
    color: #585858;
}

.TopicPage .Topic .Answers .Answer {
    margin-top: 16px;
}

.TopicPage .Topic form {
    margin-bottom: 16px;
}
.TopicPage .Topic .AnswerInput {
    margin-bottom: 0px;
}
.TopicPage .Topic .AnswerInput textarea {
    width: 100%;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    border: 1px solid #E1E1E3;
    padding: 16px;
}

.TopicPage .Topic .UserAnswerEdit p {
    overflow: scroll;
    white-space: pre-wrap;
    max-height: 64px;
    margin-bottom: 8px;
}
.TopicPage .Topic .UserAnswerEdit button{
    margin-top: 5px;
    margin-bottom: 0px;
}

.TopicPage .Topic .UserAnswerEdit {
    margin-top: 8px;
}

.TopicPage .Topic .UserAnswerEdit span {
    color: #2CC5FF;
    font-size: 16px;
}

.TopicPage .Topic .AnswerInput {
    margin-top: 8px;
}