.TopicsPage .Topic {
    margin-bottom: 16px;
}

.TopicsPage a:hover {
    text-decoration: none;
}

.TopicsPage .Topic .TopicWrapper .Title {
  color: black;
}

.TopicsPage .Topic .TopicWrapper .Description {
    margin-bottom: 0;
}

.TopicsPage .PageSubtitle {
    margin-top: 48px;
}
.TopicsPage .PageTitle {
    font-weight: 900;
    font-size: 40px;
    margin-bottom: 32px;
}

@media only screen and (max-width: 768.1px) {
    .TopicsPage .TopicWrapper {
        padding: 16px;
    }
    .TopicsPage .TopicColumn {
        padding-left: 8px;
        padding-right: 8px;
    }
}
@media only screen and (max-width: 375.6px) {
    .TopicsPage .TopicWrapper {
        padding: 16px;
    }
    .TopicsPage .TopicColumn {
        padding-left: 4px;
        padding-right: 4px;
    }
    .TopicsPage .Topic .Title {
        font-size: 24px;
    }
    .TopicsPage .Topic .Description {
        font-size: 16px;
    }
}
