.Topic {
    background: #FFFFFF;
    box-shadow: 0 1px 3px 0 #D9DDE5;
    border-radius: 5px;
}

.Topic .TopicWrapper {
    padding: 32px;
}

.Topic .Title {
    font-size: 32px;
    font-weight: 700;
    margin-bottom: 0;
    margin-top: 0;
}

.Topic .Description {
    margin-top: 8px;
    color: #585858;
}

.Topic .Answers .Answer {
    margin-bottom: 16px;
}

.Topic form {
    margin-bottom: 16px;
}
.Topic .AnswerInput {
    margin-bottom: 0px;
}
.Topic .AnswerInput textarea {
    width: 100%;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    border: 1px solid #E1E1E3;
    padding: 16px;
}

.Topic .UserAnswerEdit p {
    overflow: scroll;
    white-space: pre-wrap;
    max-height: 64px;
    margin-bottom: 8px;
}
.Topic .UserAnswerEdit button{
    margin-top: 5px;
    margin-bottom: 0px;
}