body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.LoginPage {

}

.LoginPage .row h1, p, #firebaseui_container{
    margin: 0 auto;
}
.LoginPage .row h1 {
    font-weight: bold;
    margin-top: 64px;
    display: block;
    font-size: 32px;
}

.LoginPage p {
    margin-bottom: 8px;
    display: block;
    font-size: 24px;
}
.Answer {
    background-color: #FAFBFC;
    color:black;
    border: 1px solid #E1E1E3;
    font-size: 16px;
    padding: 16px;
    border-radius: 2px;
}

.Answer .Body {
    margin-top: 0px;
    margin-bottom: 8px;
    white-space: pre-wrap;
}

.Answer .DisplayName {
    font-weight: 500;
    color: #2CC5FF;
    font-size: 14px;
    margin-bottom: 0px;
}
.Topic {
    background: #FFFFFF;
    box-shadow: 0 1px 3px 0 #D9DDE5;
    border-radius: 5px;
}

.Topic .TopicWrapper {
    padding: 32px;
}

.Topic .Title {
    font-size: 32px;
    font-weight: 700;
    margin-bottom: 0;
    margin-top: 0;
}

.Topic .Description {
    margin-top: 8px;
    color: #585858;
}

.Topic .Answers .Answer {
    margin-bottom: 16px;
}

.Topic form {
    margin-bottom: 16px;
}
.Topic .AnswerInput {
    margin-bottom: 0px;
}
.Topic .AnswerInput textarea {
    width: 100%;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    border: 1px solid #E1E1E3;
    padding: 16px;
}

.Topic .UserAnswerEdit p {
    overflow: scroll;
    white-space: pre-wrap;
    max-height: 64px;
    margin-bottom: 8px;
}
.Topic .UserAnswerEdit button{
    margin-top: 5px;
    margin-bottom: 0px;
}
.TopicsPage .Topic {
    margin-bottom: 16px;
}

.TopicsPage a:hover {
    text-decoration: none;
}

.TopicsPage .Topic .TopicWrapper .Title {
  color: black;
}

.TopicsPage .Topic .TopicWrapper .Description {
    margin-bottom: 0;
}

.TopicsPage .PageSubtitle {
    margin-top: 48px;
}
.TopicsPage .PageTitle {
    font-weight: 900;
    font-size: 40px;
    margin-bottom: 32px;
}

@media only screen and (max-width: 768.1px) {
    .TopicsPage .TopicWrapper {
        padding: 16px;
    }
    .TopicsPage .TopicColumn {
        padding-left: 8px;
        padding-right: 8px;
    }
}
@media only screen and (max-width: 375.6px) {
    .TopicsPage .TopicWrapper {
        padding: 16px;
    }
    .TopicsPage .TopicColumn {
        padding-left: 4px;
        padding-right: 4px;
    }
    .TopicsPage .Topic .Title {
        font-size: 24px;
    }
    .TopicsPage .Topic .Description {
        font-size: 16px;
    }
}

